main {


    section {
        min-height: 100dvh;
    }

    section#technologies {

        background: #ddd;
        padding: 1rem;
        display: flex;
        flex-wrap: wrap;
        gap: 1rem;



        .language-box {
            background-color: white;
            border: 1px solid #ddd;
            border-radius: 8px;
            padding: 2rem 3rem;
            text-align: center;
            box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
            transition: transform 0.3s ease, box-shadow 0.3s ease;
            cursor: pointer;

            &:hover {
                transform: translateY(-5px);
                box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
            }

            img {
                width: 50px;
                height: 50px;
                object-fit: cover;
                margin-bottom: 10px;
            }

            h3 {
                margin: 0;
                font-size: 18px;
            }
        }
    }


    section#resources {

        ul {
            display: flex;
            list-style: none;
            gap: 1rem;

            li {
                img {
                    width: 250px;
                    border-radius: 10px;
                }
            }
        }
    }
}